body{
    font-family: "Montserrat";
    margin: 0;
}

.banner {
    position: relative;
    width: 100%;
    height: 223px;
    border-radius: 25px;
    margin-bottom: 5%;
}

.ban {
    position: relative;
    width: 100%;
    height: 223px;
    border-radius: 25px;
    margin-bottom: 5%;
}

.banner_text_bg{
    position: relative;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 30%;
    border-radius: 25px;
    z-index: 2;
}

.banner_text{
    position: absolute;
    width: 100%;
    height: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
    color: white;
    font-size: 48px;
    z-index: 3;
    top:0;
    margin: 0;
}

.footer{
    padding: 2% 0;
    text-align: center;
    background-color: black;
    color: white;
    bottom: 0; 
}

.footer_text{
    font-size: 24px;
    margin-top:1.3em ;
    margin-bottom: 0;
}

@media screen and (max-width: 991px){
    .banner_text{
        font-size: 40px;
    }
}

@media screen and (max-width: 768px){
    .banner_text{
        font-size: 24px;
        width: 80%;
        left: 20px;
        line-height: 24px;
    }
    
    .ban{
    height: 223px;
    }

    .banner{
        height: 111px;
    }

    .footer_text{
        font-size: 12px;
    }
   .footer{
       width: 100%;
       padding: 2% 0 11%;
   }
}